import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Detalle } from '../interfaces/detalle';
@Injectable({
  providedIn: 'root'
})
export class ExamenService {

  //API_ENDPOINT = 'http://127.0.0.1:8000/api/';
  API_ENDPOINT = 'http://127.0.0.1:8000/api/appExamenIsw2/public/api/';
  constructor(private httpClient: HttpClient) { }
  detalle: Detalle[];

  get(){    
    return this.httpClient.get(this.API_ENDPOINT + 'resoluciones');    
  }

  getDetalles(){    
    return this.httpClient.get(this.API_ENDPOINT + 'detalles');    
  }

  cambiarExcluido(oDetalle: Detalle){ 
    const headers = new HttpHeaders({'Content-Type':'application/json'});
    return this.httpClient.post(this.API_ENDPOINT + 'cambiar/'+ oDetalle.id , oDetalle, {headers:headers});    
  }

  /*cambiarExcluido(id: number){
    const headers = new HttpHeaders({'Content-Type':'application/json'});
    return this.httpClient.post(this.API_ENDPOINT + 'cambiar/'+ id, {headers:headers});
  }*/
}
