import { Component, OnInit } from '@angular/core';
import { ExamenService } from '../services/examen.service';
import { Resolucion } from '../interfaces/resolucion'
import { ToastrService } from 'ngx-toastr';
import { Title } from '@angular/platform-browser';
import { Subject } from 'rxjs';
import { Globals } from '../globals';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: []
})
export class HomeComponent implements OnInit {
  resoluciones: Resolucion[];
  originalResoluciones: Resolucion[];
  annos: number[] = [];
  tipos: string[] = [];
  periodos: string[] = [];
  private gridApi;
  private gridColumnApi;
  private columnDefs;
  private sortingOrder;
  private localeText;
  private paginationPageSize;
  private rowClassRules;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  
  selectedOptionPeriodo: string = '';
  selectedOptionAnno: string = '';
  selectedOptionTipo: string = '';

  constructor(private examenService: ExamenService, private toastr: ToastrService,
    private titleService: Title, public globals: Globals) {
    this.globals.subtitulo = 'Resoluciones';   
  }

  setTitle(newTitle: string) {
    this.titleService.setTitle(newTitle);
  }
  ngOnInit() {
    
    this.dtOptions = {
      searching: false,
      
      pagingType: 'full_numbers',
      pageLength: 5,
      processing: true,
      language: {
        processing: "Procesando...",
        search: "Buscar:",
        lengthMenu: "Mostrar _MENU_ elementos",
        info: "Mostrando desde _START_ al _END_ de _TOTAL_ elementos",
        infoEmpty: "Mostrando ningún elemento.",
        infoFiltered: "(filtrado _MAX_ elementos total)",
        infoPostFix: "",
        loadingRecords: "Cargando registros...",
        zeroRecords: "No se encontraron registros",
        emptyTable: "No hay datos disponibles en la tabla",
        paginate: {
          first: "Primero",
          previous: "Anterior",
          next: "Siguiente",
          last: "Último"
        },
        aria: {
          sortAscending: ": Activar para ordenar la tabla en orden ascendente",
          sortDescending: ": Activar para ordenar la tabla en orden descendente"
        }
      }
    };
    
    this.examenService.get().subscribe((data: Resolucion[]) => {
      this.resoluciones = data;
      this.originalResoluciones = data;
      this.dtTrigger.next();
      this.getAnnos();
      this.getTipos();
      this.getPeriodos();
    }, (error) => {
      console.log(error);
      alert('Ocurrió un error');
    });

  }
  mostrarNotificacion(estado) {

    switch (estado) {
      case 'Aplicado':
        this.toastr.success('Se muestran los Aplicados', 'Éxito');
        break;
      case 'Pendiente':
        this.toastr.error('Se muestran los Pendientes', 'Éxito');
        break;
      case 'Ambos':
        this.toastr.info('Se muestran Aplicados y Pendientes', 'Éxito');
        break;
    }
  }

  deleteItem(id) {
    for (let i = 0; i < this.resoluciones.length; i++) {
      if (this.resoluciones[i]['id'] == id) {
        this.resoluciones.splice(i, 1);
      }
    }
  }
  getGridBusqueda(){
    this.resoluciones = this.originalResoluciones;
    var aux: string[] = [];
    var estado: boolean = false;
    var filtro: string = 'this.resoluciones = this.resoluciones.filter(resolucion =>';

    if(this.selectedOptionPeriodo !== ''){
      aux.push('resolucion.periodo.codigo === "' + this.selectedOptionPeriodo +'"')
      estado = true;
    }
            
    if(this.selectedOptionAnno !== ''){
      aux.push('resolucion.periodo.anno ===  ' + this.selectedOptionAnno )  
      estado = true;  
    }
              
    if(this.selectedOptionTipo !== ''){
      aux.push('resolucion.tipores === "' + this.selectedOptionTipo +'"' )      
      estado = true; 
    }           
     if(aux.length > 0){
      for (let i = 0; i < aux.length; i++) {
        if((aux.length - 1) === i)
        filtro = filtro + aux[i] + ');';
        else
        filtro = filtro + aux[i] + ' && ';     
        }
        console.log(aux.length);
        return eval(filtro);
    }
      
      if(aux.length === 0)
      return 11;
        
    
  }
  getGridEstado(value) {
    this.mostrarNotificacion(value)
    if (!value)
      return ''
    if (value === 'Ambos')
      return this.resoluciones = this.originalResoluciones;
    else
      this.resoluciones = this.originalResoluciones;
    return this.resoluciones = this.resoluciones.filter(
      resolucion => resolucion.estado === value 
      
    );
  }


  getAnnos() {
    for (let i = 0; i < this.resoluciones.length; i++) {
      this.annos.push(this.resoluciones[i]['periodo']['anno']);
    }
    this.annos = this.annos.filter((el, i, a) => i === a.indexOf(el))
  }

  getTipos() {
    for (let i = 0; i < this.resoluciones.length; i++) {
      this.tipos.push(this.resoluciones[i]['tipores']);
    }
    this.tipos = this.tipos.filter((el, i, a) => i === a.indexOf(el))
  }

  getPeriodos() {
    for (let i = 0; i < this.resoluciones.length; i++) {
      this.periodos.push(this.resoluciones[i]['periodo']['codigo']);
    }
    this.periodos = this.periodos.filter((el, i, a) => i === a.indexOf(el))
  }

}


