import { Component, OnInit } from '@angular/core';
import { Resolucion } from '../interfaces/resolucion';
import { ExamenService } from '../services/examen.service';
import { ActivatedRoute } from '@angular/router';
import { Globals } from '../globals';
import { Detalle } from '../interfaces/detalle'
import { Subject } from 'rxjs';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-detalle',
  templateUrl: './detalle.component.html',
  styleUrls: ['./detalle.component.css'],
  providers: [ ]
})
export class DetalleComponent implements OnInit {
  
  txtExcluido = 'sinexcluir';
  isChecked = '';
  search='';
  detalles: Detalle[];
  detallesOriginal: Detalle[];
  resolucion: Resolucion= null;/*{
    codigo: null,
    periodo: null,
    tipoRes: null,
    estado: null,
    fecha: null
  };*/
  resoluciones: Resolucion[];
  id: any;
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  txtbusqueda: boolean = true;
  oDetalle: Detalle = null;

  constructor(private examenService: ExamenService, private activatedRoute: ActivatedRoute, 
    public globals: Globals, private toastr: ToastrService) { 
    this.globals.subtitulo = 'Avales Y Resoluciones';
    this.id = this.activatedRoute.snapshot.params['id']; //Se pone id porqué en la ruta del app.module.ts se puso así
    if(this.id){
      this.examenService.get().subscribe((data: Resolucion[]) => {
        this.resoluciones = data;
        this.resolucion = this.resoluciones.find((m) => {return m.id == this.id});
        console.log(this.resolucion);
          }, (error) => {  
          console.log(error);
          alert('Ocurrió un error');
          });
        }     
         
  }

  ngOnInit() {
    this.dtOptions = {
      searching: false,
      pagingType: 'full_numbers',
      pageLength: 5,
      processing: true,
      language: {
        processing: "Procesando...",
        search: "Buscar:",
        lengthMenu: "Mostrar _MENU_ elementos",
        info: "Mostrando desde _START_ al _END_ de _TOTAL_ elementos",
        infoEmpty: "Mostrando ningún elemento.",
        infoFiltered: "(filtrado _MAX_ elementos total)",
        infoPostFix: "",
        loadingRecords: "Cargando registros...",
        zeroRecords: "No se encontraron registros",
        emptyTable: "No hay datos disponibles en la tabla",
        paginate: {
          first: "Primero",
          previous: "Anterior",
          next: "Siguiente",
          last: "Último"
        },
        aria: {
          sortAscending: ": Activar para ordenar la tabla en orden ascendente",
          sortDescending: ": Activar para ordenar la tabla en orden descendente"
        }
      }
    };

    this.examenService.getDetalles().subscribe((data: Detalle[]) => {
      this.detalles = data;
      this.detallesOriginal = data;      
      this.dtTrigger.next();
      this.detalles = this.detalles.filter(
        detalle => detalle.estado_excluido === 0
        );
    }, (error) => {
      console.log(error);
      alert('Ocurrió un error');
    });
    
  }

  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  }

  

  llenarArrayOriginal(){
    this.examenService.getDetalles().subscribe((data: Detalle[]) => {
      this.detallesOriginal = data;
          
    }, (error) => {
      console.log(error);
      alert('Ocurrió un error');
    });
    //this.getGridEstado(this.txtExcluido);
    
  }
  deleteItem(id) {
    for (let i = 0; i < this.detalles.length; i++) {
      if (this.detalles[i]['id'] == id) {
        this.detalles.splice(i, 1);
      }
    }
  }

  getGridEstado(value) {
    //this.mostrarNotificacion(value);
    if (!value)
      return ''
    if (value === 'excluido'){
      this.txtExcluido = 'excluido'
      this.mostrarNotificacion(this.txtExcluido)
      this.detalles = this.detallesOriginal;
      this.detalles = this.detalles.filter(
        detalle => detalle.estado_excluido == 1
        );
    } 
    else{
      this.txtExcluido = 'sinexcluir'
      this.mostrarNotificacion(this.txtExcluido)
      this.detalles = this.detallesOriginal;
      this.detalles = this.detalles.filter(
        detalle => detalle.estado_excluido == 0
        );
    }
    
  }

  mostrarNotificacion(estado) {

    switch (estado) {
      case 'sinexcluir':
        this.toastr.success('Se muestran Sin Excluir', 'Sin Excluir');
        break;
      case 'excluido':
        this.toastr.error('Se muestran los Excluidos', 'Excluidos');
        break;
      case 'Ambos':
        this.toastr.info('Se muestran Aplicados y Pendientes', 'Éxito');
        break;
    }
  }

  checkValue(event: any){
    if(this.txtbusqueda){
      this.txtbusqueda = false      
    }    
    else {
      
      this.txtbusqueda = true;
      this.search = ''; 
      this.detalles = this.detallesOriginal;
      this.detalles = this.detalles.filter(
        detalle => detalle.estado_excluido == 0
        );
    }
    console.log(this.txtbusqueda);   
 }

 cambiar(id: number){
   
  if(id != null){
  
  this.oDetalle =this.detalles.find((m) => {return m.id == id});
  this.examenService.cambiarExcluido(this.oDetalle).subscribe((data: number) => {
    this.detallesOriginal.find(item => item.id == id).estado_excluido = data;    
    this.deleteItem(id);
    console.log(data);
    
    if(data != null){
      if(data === 1){
        this.toastr.info('Se cambió a Excluido el estudiante: ' + this.oDetalle.nombre + ' Céd:'
        + this.oDetalle.identificacion, 'Éxito');
        return;
      }
      if(data === 0){
        this.toastr.info('Se cambió a SIN EXCLUIR el estudiante: ' + this.oDetalle.nombre + ' Céd:'
        + this.oDetalle.identificacion, 'Éxito');
        return;
      }
    }else{
      this.toastr.error('Ha ocurrido un error', 'Error');
      return;
    }
    
  }, (error) => {
    console.log(error);
    alert('Ocurrió un error');
  });
  }  
 }
}
