import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'busqueda'
})
export class BusquedaPipe implements PipeTransform {

  transform(value: any, searchValue): any {
    if (!searchValue) return value;
    return value.filter((v) => v.identificacion.toLowerCase().indexOf(searchValue.toLowerCase()) > -1 || v.identificacion.toLowerCase().indexOf(searchValue.toLowerCase()) > -1)
  }

}
